import React from 'react'
// import _ from 'lodash'

import './SegmentControls.scss'

export default function SegmentControls({onChange,...props}){
	const value=Number.isInteger(props.value)?props.value:-1
	return (
		<div {...props} className={'segment-controls row '+props.className}>
			<div
				className="highlight"
				style={{
					transform: `translateX(${value*100}%)`,
					width: 100/props.children.length+'%',
				}}
			/>
			{props.children.map((c,i)=><div key={i} {...props.childProps} onClick={()=>onClick(i)}>{c}</div>)}
		</div>
	)

	function onClick(index){
		try{ onChange(index,value) }catch{}
	}
}
