import React,{useContext,/*Fragment*/} from 'react'

import {MainContext} from '../../contexts/MainContext'
import './HistoryTimeline.scss'
import PoseToImg from '../PoseToImg/PoseToImg'

// NOTE: Maybe UI Toggle
// TODO: Scroll

export let currentPose
export default function HistoryTimeline(){
	const {state,dispatch}=useContext(MainContext)
	const {historySeekPos,poseHistory,simPose,historyLockHand,historySeekbarPrecision}=state
	if(historySeekPos===poseHistory.length) currentPose=JSON.stringify(simPose)
	return (
		<div id="HistoryTimeline">
			<div className="row">
				<div className="f-center px-8 py-4 spb-x-8">
					<div className="fa fa-history"/>
					<div className="b-700">History Timeline</div>
				</div>
				<div className="f-center px-8 py-4 pr-0">
					<span className="h7">Don't affect Hand State</span>
					<input
						className="ml-8"
						type="checkbox"
						checked={historyLockHand}
						onChange={({target})=>{dispatch(['SET_HISTORY_LOCK_HAND',target.checked])}}
					/>
				</div>
				<div className="f-center px-8 py-4 pr-0" style={{visibility: poseHistory.length?'visible':'hidden'}}>
					<span className="h7">Slider position</span>
					<input
						className="mx-8 text-center h7 b-500"
						type="number"
						value={historySeekPos}
						onChange={({target:{valueAsNumber}})=>dispatch(['HISTORY_SEEK',valueAsNumber])}
					/>
				</div>
				{/*<div className="f-center px-8 py-4 pr-0">
					<span className="h7">Smooth Seekbar</span>
					<input
						className="ml-8"
						type="checkbox"
						checked={historySeekbarPrecision==2}
						onChange={({target})=>{dispatch(['SET_STATE',{historySeekbarPrecision: target.checked?2:1}])}}
					/>
				</div>*/}
				<div className="fa fa-minus f-center px-16 ml-auto" onClick={()=>dispatch(['SET_STATE',{showHistoryTimeline: false}])}/>
			</div>
			<div className="seekbar f-center" style={{height: '30px'}}>
				<div className="text-center h7">Preview</div>
				{
					poseHistory.length>=1?
						<input
							type="range"
							min="0"
							max={poseHistory.length}
							step={1/10**historySeekbarPrecision}
							value={historySeekPos}
							onChange={({target:{valueAsNumber}})=>dispatch(['HISTORY_SEEK',valueAsNumber])}
							style={{width: (poseHistory.length)*136+'px'}}
						/>:undefined
				}
			</div>
			<div className="row spc-8 f-nowrap scroll-x">
				{poseHistory.map((p,i)=><PoseToImg key={i} pose={p} onClick={()=>dispatch(['HISTORY_SEEK',i])}/>)}
				<div className="col-extrude p-8 current d-flex">
					<PoseToImg cache={false} pose={currentPose} onClick={()=>dispatch(['HISTORY_SEEK',poseHistory.length])}/>
					<div className="vertical-text h7 b-500 f-center">
						<div className="mb-auto">Current Pose</div>
						<div className="fa fa-chevron-right"/>
					</div>
				</div>
			</div>
		</div>
	)
}
