import React,{createContext,useReducer,useEffect} from 'react'
import {withRouter} from 'react-router-dom'
import _ from 'lodash'
import axios from 'axios'

import {mainReducer} from '../reducers/mainReducer'
import sections from '../model/Sixi2'
import {currentState} from '../reducers/mainReducer'

export let dispatch
export const prodMode=true

// TODO: Update port
export const homePose=JSON.stringify(_.mapValues(sections,s=>s.angles.home))
export let realPose=JSON.parse(homePose)
export let targetPose=JSON.parse(homePose)
export const initState=JSON.stringify({
	isPolling: prodMode?true:false,
	IP: '',

	simPose: JSON.parse(homePose),
	online: false,
	camFeedState: 'searching',
	camFeedMaximize: false,
	isPoseToImgReady: false,

	showHistoryTimeline: true,
	showSequenceControl: false,
	showLibrary: false,

	poseHistory: [],
	historySeekPos: 0,
	historyLockHand: true,
	historySeekbarPrecision: 2,
})
export const MainContext=createContext(JSON.parse(initState))
export const current={} // state,dispatch
export const urlStateParamList=['IP','isPolling']

let mounted=false
export default withRouter(function MainContextProvider({children,location,history}){
	let state
	([state,dispatch]=useReducer(mainReducer,JSON.parse(initState)))

	useEffect(componentDidMount,[])
	useEffect(stateToUrl,[...urlStateParamList.map(p=>state[p]),location.pathname])
	useEffect(onSetIP,[state.IP])

	// console.log('MainContextProvider render')
	return <MainContext.Provider value={{state,dispatch}}>{children}</MainContext.Provider>

	function componentDidMount(){
		console.log('componentDidMount')
		const stateFromUrl=_.pick(Object.fromEntries(new URLSearchParams(location.search)),urlStateParamList)
		dispatch(['SET_STATE',stateFromUrl])
		window.setInterval(()=>{
			if(currentState.isPolling){
				// console.log(currentState)
				axios.get(`https://${currentState.IP}:7878/live`).then(res=>{
					// console.log(res.data)
					// dispatch(['SET_ACTUAL_ANGLES',res.data])
					realPose={...JSON.parse(homePose),...res.data.position}
					targetPose={...JSON.parse(homePose),...res.data.target}
					dispatch(['SET_STATE',{online: true}])
				}).catch(err=>dispatch(['SET_STATE',{online: false}]))
			}
		},1000)
	}

	function stateToUrl(){
		console.log('stateToUrl')
		if(mounted)
			history.replace(location.pathname+'?'+new URLSearchParams(_.pick(state,urlStateParamList)).toString())
		else{
			// history.replace('/login?'+new URLSearchParams(_.pick(state,urlStateParamList)).toString()) // TODO: Remove
			mounted=true
		}
	}

	// TODO: Robot nickname, firmware versions
	function onSetIP(){
		dispatch(['SET_STATE',{camFeedState: 'searching'}])
	}
})
