import React,{Fragment,useState} from 'react'
import './Accordion.css'

// TODO: headProps,bodyProps,bodyInnerProps
export default function Accordion(props){
	const [state,setState]=useState({expanded: true,maxHeight: undefined})

	return (
		<Fragment>
			<div
				id={props.id}
				className={'accordion'+(props.className ?? '')+(state.expanded?' expanded':'')}
				{...{onClick}}
			>{props.head}</div>
			<div style={{maxHeight: state.maxHeight}}>
				<div>{props.children}</div>
			</div>
		</Fragment>
	)

	function onClick({currentTarget}){
		const maxHeight=currentTarget.nextSibling.children[0].offsetHeight
		currentTarget.nextSibling.style.maxHeight=maxHeight+'px'
		// setExpanded(!expanded)
		setTimeout(()=>setState({expanded: !state.expanded,maxHeight}),0)
	}
}
