import * as THREE from 'three'
import {OBJLoader} from 'three/examples/jsm/loaders/OBJLoader'
export const objLoader=new OBJLoader()

export function objLoaderAsync(path){
	return new Promise((resolve,reject)=>{
		try{ objLoader.load(path,o=>resolve(o),null,null,null) }
		catch(e){ reject(e) }
	})
}

// Returns angle between (-180,180]
export function normalizeAngle(angle){
	angle=angle%360
	angle=(angle+360)%360
	if(angle>180) angle-=360
	return angle
}

export function setupRenderer(width,height,renderer,camera){
	const aspectRatio=width/height

	renderer.setSize(width,height)
	renderer.setPixelRatio(window.devicePixelRatio)
	// renderer.setPixelRatio(1) // Low quality, high performance for higher density displays

	// Universal desktop and mobile friendly FOV algorithm
	camera.fov=45*(aspectRatio>1?1:Math.atan(1/aspectRatio)/THREE.Math.DEG2RAD/45)
	camera.aspect=aspectRatio
	camera.updateProjectionMatrix()
}
