import React,{useContext,useState,useEffect} from 'react'
import {withRouter} from 'react-router-dom'

import './Login.scss'
import {MainContext} from '../../contexts/MainContext'

export default withRouter(function Login({history}){
	const {state,dispatch}=useContext(MainContext)
	const [isMounted,setIsMounted]=useState(false)

	useEffect(()=>setIsMounted(true),[])

	// console.log('Login render')
	return (
		<form id="Login" className={isMounted?'active':undefined} {...{onSubmit}}>
			<section>
				{isMounted?<style>
					#cam-feed,
					#cam-feed:hover:not(.active)&#123;
						z-index: 1;
						transform: scale(1,1);
						right: calc(50% - 320px);
						bottom: calc(50% - 213px);
						border-radius: 6px;
						transition: opacity .5s;
					&#125;
				</style>:undefined}
				<div className="cam-feed-container"/>
				<div className="row spc-12 f-center">
					<label htmlFor="login-ip">Robot Address / IP</label>
					<div className="button outline text-muted" style={{borderRadius: '4px 0 0 4px',cursor: 'initial'}}>https://</div>
					<input
						id="login-ip"
						className="col col-extrude-l"
						style={{borderRadius: '0 4px 4px 0'}}
						type="text"
						name="Robot IP"
						value={state.IP}
						onChange={({target})=>dispatch(['SET_STATE',{IP: target.value}])}/>
					<button className="outline">Go</button>
				</div>
			</section>
		</form>
	)

	function onSubmit(e){
		e.preventDefault()
		setIsMounted(false)
		window.setTimeout(()=>history.push('/'),500)
	}
})
