import React,{useContext,Fragment} from 'react'

import './CamFeed.scss'
import {MainContext} from '../../contexts/MainContext'

// TODO: camFeedState!=='found'
export default function CamFeed({className,children}){
	const {state,dispatch}=useContext(MainContext)
	return (
		<Fragment>
			<style>
				#cam-feed&#123;
					{state.camFeedState!=='found'?'opacity: 0':undefined}
				&#125;
			</style>
			<img id="cam-feed"
				className={state.camFeedMaximize?'active':''}
				src={`http://${state.IP}:8081/?action=stream`}
				alt="Cam feed"
				onClick={()=>dispatch(['SET_STATE',{camFeedMaximize: !state.camFeedMaximize}])}
				onLoad={()=>dispatch(['SET_STATE',{camFeedState: 'found'}])}
				onError={()=>dispatch(['SET_STATE',{camFeedState: 'not found'}])}
			/>
		</Fragment>
	)
}
