import React,{useContext} from 'react'
import * as THREE from 'three'
// import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls'
import _ from 'lodash'

import './PoseToImg.scss'
import {postObjLoad} from '../SimRobot/SimRobot'
import {MainContext,dispatch} from '../../contexts/MainContext'
import {setupRenderer} from '../../three-js-helper'

// NOTE: Maybe blobs (about 20% better memory efficiency)

const renderer=new THREE.WebGLRenderer({
	antialias:             true,
	alpha:                 true,
	preserveDrawingBuffer: true,
})
const canvas=renderer.domElement
canvas.id='PoseToImgCanvas'
document.body.appendChild(canvas)

let scene
let section3dObjs={}
const section3dObjects={}

const camera=new THREE.PerspectiveCamera()
camera.position.set(100,100,100)
camera.lookAt(0,30,0)
// const controls=new OrbitControls(camera,canvas)
// controls.enableDamping=true
// controls.enablePan=false
// controls.dampingFactor=.1
// controls.rotateSpeed=.5
// controls.maxPolarAngle=Math.PI/2
// controls.target.set(0,30,0)
setupRenderer(320,240,renderer,camera)

function render(){
	// requestAnimationFrame(render)
	// controls.update()
	renderer.render(scene,camera)
}

export function initPoseToImg(s,so){
	scene=s
	section3dObjs=so
	// console.log(scene,section3dObjs)

	postObjLoad(scene,section3dObjs,section3dObjects)
	console.log('PoseToImgReady')
	dispatch(['SET_STATE',{isPoseToImgReady:true}])
}

const imgCache=/*JSON.parse(localStorage.getItem('imgCache')) ??*/ {}
export default function PoseToImg({pose,cache,...props}){
	const {state}=useContext(MainContext)
	let thumbnail=imgCache[pose]

	if(state.isPoseToImgReady && !thumbnail){
		const poseObj=JSON.parse(pose)
		_.forIn(section3dObjects,(s,i)=>s.rotateSection(poseObj[i]))
		render()
		thumbnail=canvas.toDataURL()
		if(cache!==false){
			imgCache[pose]=thumbnail
			// try{ localStorage.setItem('imgCache',JSON.stringify(imgCache)) }catch(e){ console.error(e) }
		}
	}

	// console.log(imgCache)
	return <img src={thumbnail} {...props} data-pose={pose} alt=""/>
}
