import React,{useContext} from 'react'

import './BottomDrawer.scss'
import {MainContext} from '../../contexts/MainContext'
import HistoryTimeline from '../HistoryTimeline/HistoryTimeline'

export default function BottomDrawer(){
	const {state,dispatch}=useContext(MainContext)
	const {showHistoryTimeline,showSequenceControl,showLibrary}=state
	return (
		<div id="BottomDrawer">
			{(showHistoryTimeline || showSequenceControl)===false?<style>:root&#123; --bottomDrawerHeight: 46px &#125;</style>:undefined}
			<div id="taskbar" className="row spc-8 f-end">
				<div className={'button outline row f-center'+(showHistoryTimeline?' active':'')} onClick={()=>dispatch(['SET_STATE',{showHistoryTimeline: !showHistoryTimeline}])}>
					<div className="fa fa-history super-center"/>
					<div className="col text-center mr-m30">History Timeline</div>
					<div className="fa fa-history super-center"/>
				</div>
				<div className={'button outline row f-center'+(showSequenceControl?' active':'')} onClick={()=>dispatch(['SET_STATE',{showSequenceControl: !showSequenceControl}])}>
					<div className="icon-layout_directed super-center"/>
					<div className="col text-center mr-m30">Sequence Controls</div>
					<div className="icon-layout_directed super-center"/>
				</div>
				<div className={'button outline row f-center'+(showLibrary?' active':'')} onClick={()=>dispatch(['SET_STATE',{showLibrary: !showLibrary}])}>
					<div className="fa fa-bookmark super-center"/>
					<div className="col text-center mr-m30">Library</div>
					<div className="fa fa-bookmark super-center"/>
				</div>
				<div className="ml-auto h7">App Version: 0.1.4</div>
			</div>
			{showHistoryTimeline?<HistoryTimeline/>:undefined}
		</div>
	)
}
