import React from 'react'
import {Link} from 'react-router-dom'

import './Header.scss'

// TODO: Robot name
export default function Header(){
	return (
		<header id="Header" className="row spc-8 spc-x-16 f-center">
			<img src={require('../../assets/imgs/icon_logo.svg')} alt=""/>
			<div className="b-600 uppercase">
				<div className="h5">Marginally Clever</div>
				<div className="h8 text-muted">Robot Overlord - III</div>
			</div>
			<div className="col"/>
			<Link to="/login" className="button-icon">
				<div className="fa fa-power-off text-muted"/>
				<div>Login</div>
			</Link>
			<Link to="/login" className="button-icon">
				<div className="fa fa-cogs text-muted"/>
				<div>Settings</div>
			</Link>
			<div className="button-icon" onClick={()=>window.location.reload()}>
				<div className="fa fa-refresh text-muted"/>
				<div>Reload</div>
			</div>
		</header>
	)
}
