import React,{useRef,useEffect,Fragment,useState} from 'react'
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls'
import {TransformControls} from 'three/examples/jsm/controls/TransformControls'
import * as THREE from 'three'
import _ from 'lodash'

import './SimRobot.scss'
import Sixi2 from '../../model/Sixi2'
import {initPoseToImg} from '../PoseToImg/PoseToImg'
import {objLoaderAsync,setupRenderer} from '../../three-js-helper'
import SegmentControls from '../../utilities/SegmentControls/SegmentControls'
import {currentState} from '../../reducers/mainReducer'
import {dispatch} from '../../contexts/MainContext'

export let canvas
export let renderer
export let scene
export let camera
export let controls
export const section3dObjs={}
export const section3dObjects={}
export let cameraPos=new THREE.Vector3(90,90,-90)
export let endAffector3dObject
export let endAffectorControls

export const objNames=['1_Anchor.obj',...Object.values(Sixi2).flatMap(o=>o.objs)].filter(o=>o)
export const objLoadPromise=Promise.all(objNames.map(o=>objLoaderAsync(require('../../assets/objs/'+o)))).then(o=>{
	o.forEach((o,i)=>section3dObjs[objNames[i]]=o)
})

export default function SimRobot(){
	canvas=useRef()
	useEffect(componentDidMount,[])
	const [endAffectorMode,setEndEffectorMode]=useState(0)

	return (
		<Fragment>
			<canvas id="SimRobot" ref={canvas}></canvas>
			<div id="IKControls" className="row spc-12">
				<div className="my-0 ml-0 button-icon" onClick={()=>controls.object.position.set(100,100,100)}>
					<div className="fa fa-repeat text-muted" style={{transform: 'rotateY(180deg)'}}/>
					<div>Reset View</div>
				</div>
				<div className="col"/>
				<div className="h7">End Affector Control Mode</div>
				<SegmentControls
					className="m-2"
					style={{width: '100px'}}
					value={endAffectorMode}
					onChange={changeEndEffectorMode}
				>
					<Fragment>Move</Fragment>
					<Fragment>Rotate</Fragment>
				</SegmentControls>
			</div>
		</Fragment>
	)

	function componentDidMount(){
		(async ()=>{
			renderer=new THREE.WebGLRenderer({
				antialias:             true,
				canvas:                canvas.current,
				alpha:                 true,
				preserveDrawingBuffer: true,
			})
			scene=new THREE.Scene()
			camera=new THREE.PerspectiveCamera()
			camera.position.set(100,100,100)
			camera.lookAt(0,30,0)
			controls=new OrbitControls(camera,renderer.domElement)
			controls.enableDamping=true
			controls.enablePan=false
			controls.dampingFactor=.1
			controls.rotateSpeed=.5
			controls.maxPolarAngle=Math.PI/2
			controls.target.set(0,30,0)
			setupThisRenderer()
			window.addEventListener('resize',setupThisRenderer,true)

			const pointLight=new THREE.PointLight()
			pointLight.position.set(0,100,0)
			scene.add(pointLight)
			const ambientLight=new THREE.AmbientLight()
			ambientLight.intensity=.25
			scene.add(ambientLight)

			await objLoadPromise
			// console.log(scene,section3dObjs)
			initPoseToImg(scene.clone(),_.mapValues(section3dObjs,o=>o.clone()))

			postObjLoad(scene,section3dObjs,section3dObjects)

			endAffector3dObject=new THREE.Mesh(
				new THREE.SphereBufferGeometry(1,32,32),
				new THREE.MeshLambertMaterial()
			)
			scene.add(endAffector3dObject)

			endAffectorControls=new TransformControls(camera,renderer.domElement)
			endAffectorControls.addEventListener('dragging-changed',({value})=>controls.enabled=!value)
			endAffectorControls.setSpace('local')
			endAffectorControls.attach(endAffector3dObject)
			scene.add(endAffectorControls)

			scene.updateMatrixWorld()
			let pos=new THREE.Vector3()
			section3dObjects['v'].getWorldPosition(pos)
			// section3dObjects['t'].inner.getWorldPosition(pos)
			// pos.add(new THREE.Vector3(12.5,1,0))
			endAffector3dObject.position.set(...pos.toArray())

			// console.log(section3dObjects['x'].getWorldPosition())
			// console.log(section3dObjects['y'].getWorldPosition())
			// console.log(section3dObjects['z'].getWorldPosition())
			// console.log(section3dObjects['u'].getWorldPosition())
			// console.log(section3dObjects['v'].getWorldPosition())
			// console.log(section3dObjects['w'].getWorldPosition())

			const yPos=new THREE.Vector3()
			section3dObjects['y'].getWorldPosition(yPos)
			const yLen=section3dObjects['z'].position.length()
			const zuLen=new THREE.Vector3().addVectors(section3dObjects['u'].position,section3dObjects['v'].position).length()
			const eaFromY=new THREE.Vector3()
			eaFromY.subVectors(endAffector3dObject.position,yPos)
			const eaLen=eaFromY.length()
			const yzInitAngle=angleFromSSSTriangle(eaLen,zuLen,yLen)*THREE.Math.RAD2DEG

			endAffectorControls.addEventListener('change',()=>{
				const eaFromY=new THREE.Vector3()
				eaFromY.subVectors(endAffector3dObject.position,yPos)
				const eaLen=eaFromY.length()

				let xAngle=-Math.atan(eaFromY.z/eaFromY.x)*THREE.Math.RAD2DEG
				if(eaFromY.x<0)
					if(eaFromY.z>0) xAngle=xAngle-180
					else xAngle=180+xAngle

				const eaXZ=eaFromY.clone().setY(0).length()
				const alpha=Math.atan(eaFromY.y/eaXZ)*THREE.Math.RAD2DEG
				const beta=angleFromSSSTriangle(zuLen,yLen,eaLen)*THREE.Math.RAD2DEG || 0
				// console.log(zuLen,yLen,eaLen,eaFromY)
				// console.log(alpha,beta)

				const zAngle=angleFromSSSTriangle(eaLen,zuLen,yLen)*THREE.Math.RAD2DEG || 180
				dispatch(['SET_SIM_ANGLES',{
					...currentState.simPose,
					x: xAngle,
					y: -alpha-beta,
					z: yzInitAngle-zAngle
				}])
			})

			render()
		})()
	}

	function changeEndEffectorMode(i){
		switch(i){
			case 0:
				endAffectorControls.setMode('translate')
				break
			case 1:
				endAffectorControls.setMode('rotate')
				break
			default:
		}
		setEndEffectorMode(i)
	}
}

export function setupThisRenderer(){
	setupRenderer(canvas.current.offsetWidth,canvas.current.offsetHeight,renderer,camera)
}

export function postObjLoad(scene,section3dObjs,section3dObjects){
	_.forIn(Sixi2,(s,i)=>section3dObjects[i]=datatTo3dObject(s,i,section3dObjs,section3dObjects))

	const section=section3dObjects['t'].inner
	const leftFork=section.children[1].inner
	const rightFork=section.children[2].inner
	section3dObjects['t'].rotateSection=function(offset){
		// NOTE: Verify and remove .7
		const factor=(offset-Sixi2['t'].angles.max)/Sixi2['t'].angles.max*.7
		leftFork.position.z=factor*Sixi2['t_l'].position[2]
		rightFork.position.z=factor*Sixi2['t_r'].position[2]
	}
	scene.add(section3dObjs['1_Anchor.obj'])
	scene.add(section3dObjects['x'])
}

export function datatTo3dObject(data,name,section3dObjs,section3dObjects){
	const axis=new THREE.Vector3(...data.axis)
	const sectionOuter=new THREE.Object3D()
	sectionOuter.name='section-'+name
	sectionOuter.position.set(...data.position)
	sectionOuter.setRotationFromAxisAngle(axis,data.angles.home*THREE.Math.DEG2RAD)

	const sectionInner=new THREE.Object3D()
	sectionOuter.add(sectionInner)
	sectionInner.name='section-'+name+'-inner'
	data.objs.map(o=>sectionInner.add(section3dObjs[o]))

	sectionOuter.sectionName=name
	sectionOuter.inner=sectionInner
	sectionOuter.axis=axis
	sectionOuter.rotateSection=function(angle){
		angle=THREE.Math.clamp(angle,data.angles.min,data.angles.max)
		if(sectionOuter.sectionAngle===angle) return
		sectionOuter.sectionAngle=angle
		sectionInner.setRotationFromAxisAngle(axis,-angle*THREE.Math.DEG2RAD)
	}
	sectionOuter.rotateSection(data.angles.home)

	if(data.parent)
		section3dObjects[data.parent].inner.add(sectionOuter)
	return sectionOuter
}

function render(){
	requestAnimationFrame(render)
	controls.update()
	cameraPos=controls.object.position
	renderer.render(scene,camera)
}

export function poseLerp(a,b,alpha){
	return _.mapValues(a,(v,i)=>THREE.Math.lerp(a[i],b[i],alpha))
}

// TODO: Add to helper
function angleFromSSSTriangle(a,b,c){
	return Math.acos((b**2+c**2-a**2)/(2*b*c))
}

